import React, { Fragment, useState, useEffect } from 'react'
import articles from '../article-content'
import CommentsList from '../components/CommentsList'
import NotFoundPage from './NotFoundPage'
import ArticlesList from '../components/ArticlesList'
import UpvoteSection from '../components/UpvotesSection'
import AddCommentForm from '../components/AddCommentForm'
import AddComentForm from '../components/AddCommentForm'

const ArticlePage = ({ match }) => {
	let name = match.params.name
	let article = articles.find(art => art.name === name)

	const [articleInfo, setArticleInfo] = useState({ upvotes: 0, comments: [] })

	useEffect(() => {
		const fetchData = async () => {
			const result = await fetch(`/api/articles/${name}`)
			const body = await result.json()

			setArticleInfo(body)
		}

		fetchData()
	}, [name])

	if (!article) {
		return <NotFoundPage />
	}

	const otherArticles = articles.filter(a => a.name !== name)

	return (
		<Fragment>
			<h1>{article.title}</h1>
			<UpvoteSection
				articleName={name}
				upvotes={articleInfo.upvotes}
				setArticleInfo={setArticleInfo}
			/>
			{article.content.map((paragraph, i) => (
				<p key={i}>{paragraph}</p>
			))}
			<CommentsList comments={articleInfo.comments} />
			<AddComentForm articleName={name} setArticleInfo={setArticleInfo} />
			<h3>Other Articles</h3>
			<ArticlesList articles={otherArticles} />
		</Fragment>
	)
}

export default ArticlePage
